html {
  box-sizing: border-box; }

* {
  margin: auto;
  box-sizing: inherit; }

body {
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  line-height: 24px;
  color: #1f1f1f;
  background-color: #f5f5f5; }

a {
  font-size: 16px;
  line-height: 50px;
  color: #1a1ff7;
  text-decoration: none;
  margin: 0; }
  a:hover {
    text-decoration: none;
    color: #6c73ff; }
  a:visited {
    text-decoration: none;
    color: #6c73ff; }

img {
  max-width: 100%;
  height: auto; }

ul,
li {
  list-style: none;
  padding-left: 0; }

.page-header {
  height: 50px;
  background-color: #fff; }
  .page-header__inner {
    display: flex;
    margin: 0 7%; }

a {
  line-height: 0; }

.logo {
  display: block;
  padding-top: 20px; }

.time {
  display: none;
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  font-style: italic;
  color: #828282; }
  @media (min-width: 768px) {
    .time {
      display: block; } }

.main-nav {
  display: block; }
  @media (min-width: 768px) {
    .main-nav {
      display: block; } }
  .main-nav__toggele {
    background-image: url(img/icon_bascket.png);
    background-repeat: no-repeat;
    height: 25px;
    width: 25px; }
    .main-nav__toggele .toggele {
      position: fixed;
      top: 19px;
      left: 87%;
      width: 22px;
      height: 2px;
      background: #1f1f1f;
      z-index: 5; }
      .main-nav__toggele .toggele:before {
        content: '';
        position: fixed;
        top: 26px;
        left: 87%;
        width: 22px;
        height: 2px;
        background: #1f1f1f;
        z-index: 5; }
      .main-nav__toggele .toggele:after {
        content: '';
        position: fixed;
        top: 33px;
        left: 87%;
        width: 22px;
        height: 2px;
        background: #1f1f1f;
        z-index: 5; }
    @media (min-width: 768px) {
      .main-nav__toggele {
        display: none; } }
  .main-nav__list {
    display: none; }
    @media (min-width: 768px) {
      .main-nav__list {
        display: block; } }

.promo {
  margin: 0 7%;
  height: 493px; }

.slider__inner {
  position: relative;
  border: 10px solid #e8e8e8;
  margin-top: 100px;
  margin-left: -5%;
  margin-right: -2%; }

.slider__item-subtitle {
  position: absolute;
  top: -80px;
  padding-left: 15px;
  font-family: "Montserrat", sans-serif;
  font-style: italic;
  font-size: 14px; }
  @media (min-width: 768px) {
    .slider__item-subtitle {
      font-size: 16px; } }

.slider__item-title {
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  line-height: 30px;
  padding-top: 50px;
  padding-left: 15px; }
  @media (min-width: 768px) {
    .slider__item-title {
      font-size: 30px;
      line-height: 40px; } }

.slider__item-descr {
  padding-top: 40px;
  padding-bottom: 30px;
  padding-left: 15px; }

.slider__item-img {
  display: none; }
  @media (min-width: 768px) {
    .slider__item-img {
      display: flex; } }

.catalog__title {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

.catalog__tabs-wrap {
  background-color: #e8e8e8; }

.catalog__tabs {
  display: flex;
  margin: 0 7%;
  padding-top: 25px;
  padding-bottom: 5px;
  padding-left: 0; }

.catalog__tab-item {
  padding-right: 30px; }

.page-banner {
  background-color: #e41284;
  height: 300px; }
  .page-banner__inner {
    margin: 0 10%;
    padding-top: 60px;
    text-align: center; }
    @media (min-width: 768px) {
      .page-banner__inner {
        padding-top: 100px; } }
    @media (min-width: 1200px) {
      .page-banner__inner {
        padding-top: 120px; } }
  .page-banner__title {
    font-size: 24px;
    line-height: 40px;
    color: #fff;
    padding-bottom: 20px; }
    @media (min-width: 768px) {
      .page-banner__title {
