    @media (min-width: 1200px) {
      .page-banner__title {
        font-size: 36px;
        line-height: 30px; } }
  .page-banner__subtitle {
    font-size: 16px;
    line-height: 30px;
    color: #fff; }

@media (min-width: 1200px) {
  .contact {
    margin: 0 7%; } }

.contact__header {
  margin: 0 7%;
  margin-top: 35px;
  margin-bottom: 35px; }
  @media (min-width: 1200px) {
    .contact__header {
      margin: 0;
      margin-top: 35px;
      margin-bottom: 35px; } }

.contact__title {
  font-family: "Montserrat", sans-serif;
  font-size: 50px;
  line-height: 70px;
  font-weight: 700; }

.contact__list {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }

.contact__item {
  margin: 0;
  padding-right: 20px;
  line-height: 50px; }
  .contact__item:before {
    content: '•';
    color: #1a1ff7;
    padding-right: 15px;
    font-family: "Montserrat", sans-serif;
    font-size: 18px;
    font-weight: 700; }
  @media (min-width: 768px) {
    .contact__item {
      padding-right: 100px; } }

.contact__map {
  width: 100%;
  height: 340px; }

.contact__form {
  position: relative;
  border: 10px solid #e8e8e8;
  margin-top: 40px;
  margin-bottom: 40px;
  margin-left: 3%;
  margin-right: 3%;
  padding-top: 50px;
  padding-bottom: 80px; }
  .contact__form:before {
    content: '';
